.temp1 {	
	background: linear-gradient(to bottom, #c2e59c, #bff1db, #eedcbc, #f0e4a2, #9bcaf1 );
	background-size: 100%;
	background-repeat: no-repeat;
}

temp1:before{
	content:'';
	position: fixed;
	top: 0;
	bottom: 0;
	width: 100%;
	z-index: -1;
	background: linear-gradient(to right bottom, rgba(255,255,255,0.2), #21D4FD 75%);
}


temp1 {
	margin: 0;
	font-size: 1.15rem;
	line-height: 1.4em;
}

main,header {
	box-sizing: border-box;
}

main {
	background-color: #f7f7f7;
	padding: 20px;
	border-radius: 10px;
	color: #6b6b6b;
	margin: 40px 0;
}

.typewritera {
    font-family: "Motiva Sans Bold";
    font-size: 4vh;

}